#footer {
  text-align:center;
  font-family: Lato;
  font-weight: 300;
  font-size: smaller;
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 2.5rem;
}
#buttons {
  width:50%;
  margin:auto;
  display:inline;
}

.nav-section {
  margin: 3% 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.nav-section > .project-item {
  display: inline-block;
  pointer-events: auto;
  cursor: pointer;
}


.nav-section > .project-item:hover {
  text-decoration: underline;
}

.project{
  /* background-color: #F8DFDB; */
  display:flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

#btn-group{
  display:flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
}